<template>
  <div class="update">
    <el-form  :model="formData" label-width="80px">
      <el-form-item label="标题">
        <el-input v-model="formData.title"></el-input>
      </el-form-item>
      <el-form-item label="链接">
        <el-input v-model="formData.url"></el-input>
      </el-form-item>
      <el-form-item label="顺序">
        <el-input v-model="formData.sort"></el-input>
      </el-form-item>
      <el-form-item label="状态">
        <el-switch
          v-model="formData.isShow"
          active-color="#13ce66"
          inactive-color="#ff4949">
        </el-switch>
      </el-form-item>
      <el-form-item label="缩略图">
        <el-upload
          class="avatar-uploader"
          action="https://upload-z1.qiniup.com"
          :show-file-list="false"
          :on-success="upload"
          :data="{token:token}"
        >
          <img v-if="formData.img" :src="formData.img" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" round @click="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
    export default{
      name: "",
      data(){
        return{
          formData:{
            title:'',
            url:'',
            sort:'',
            isShow:'',
            img:''
          },
          token:''
        }
      },
      methods:{
        //数据回填
        getData() {
          this.$axios.get('getCarousels',{id: this.$route.query.id}).then(res => {
            this.formData = res.data[0]
          })
        },
        getToken(){
          this.$axios.qiniuGet().then(res => {
            this.token = res.data
          })
        },
        upload(res){
          this.formData.img = res.url
        },
        //修改
        submit(){
          let params = {
            ...this.formData,
            id:this.$route.query.id
          }
          this.$axios.post('updateCarousels',params).then( res => {
            if(res.code == 200){
              this.$message({
                message:`${res.msg}`,
                type:'success'
              })
            }else{
              this.$message({
                message:`${res.msg}`,
                type:'warning'
              })
            }
            setTimeout( () => {
              this.$router.push('/carousel')
            },1500)
          })
        }
      },
      created(){
        this.getToken();
        this.getData()
      }
    }
</script>
<style scope>
  .update{
    height:100%;
    padding-top: 20px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  /*.update .el-form-item .el-upload{*/
    /*width: 190px;*/
    /*height: 190px;*/
  /*}*/
  /*.update .el-form-item .el-upload img{*/
    /*width: 190px;*/
    /*height: 190px;*/
  /*}*/
</style>



